import { cache, getEnabledElement, StackViewport } from '@cornerstonejs/core';
import type { Types } from '@cornerstonejs/core';

import { BaseTool } from '../base';
import {
    PublicToolProps,
    ToolProps,
    EventTypes,
    SVGDrawingHelper,
} from '../../types';
import { fillReplace } from "./strategies";
import {activeSegmentation, segmentLocking, state as segmentationState} from "../../stateManagement/segmentation";
import {LabelmapSegmentationDataVolume} from "../../types/LabelmapTypes";

class UndoTool extends BaseTool {
    static toolName;
    _throttledCalculateCachedStats: any;
    editData: {
        annotation: any;
        segmentationId: string;
        segmentation: any;
        segmentIndex: number;
        segmentsLocked: number[];
        segmentColor: [number, number, number, number];
        viewportIdsToRender: string[];
        handleIndex?: number;
        movingTextBox: boolean;
        newAnnotation?: boolean;
        hasMoved?: boolean;
    } | null;
    isDrawing: boolean;
    isHandleOutsideImage: boolean;

    constructor(
        toolProps: PublicToolProps = {},
        defaultToolProps: ToolProps = {
            supportedInteractionTypes: ['Mouse', 'Touch'],
            configuration: {
                strategies: {
                    FILL_REPLACE: fillReplace
                },
                defaultStrategy: 'FILL_REPLACE',
                activeStrategy: 'FILL_REPLACE',
            },
        }
    ) {
        super(toolProps, defaultToolProps);
    }

    onSetToolPassive = () => {
        window.addEventListener('UNDO_REDO_TOOL', this.applyReplaceCallback)
    };

    applyReplaceCallback = (evt) => {
        const {element, oldScalarData} = evt.detail
        const toolGroupId = this.toolGroupId;

        const activeSegmentationRepresentation =
            activeSegmentation.getActiveSegmentationRepresentation(toolGroupId);
        if (!activeSegmentationRepresentation) {
            throw new Error(
                'No active segmentation detected, create one before using the brush tool'
            );
        }

        const { segmentationId, type } = activeSegmentationRepresentation;

        const { representationData } =
            segmentationState.getSegmentation(segmentationId);

        // Todo: are we going to support contour editing with this tool?
        const { volumeId } = representationData[type] as LabelmapSegmentationDataVolume;
        const segmentation = cache.getVolume(volumeId);

        const operationData = {
            volume: segmentation,
            segmentationId,
            oldScalarData
        }

        const currentElement = getEnabledElement(element)
        this.applyActiveStrategy(currentElement, operationData)
    }


}

UndoTool.toolName = 'Undo';
export default UndoTool;
